import { Outlet, useLocation, useParams } from 'react-router-dom';
import {
  Back,
  Box,
  LinkList,
  Section,
  StyledLink,
} from './MovieDetails.styled';
import { Suspense, useEffect, useRef, useState } from 'react';
import { getMovieDetails } from 'services/api';
import { IMG_URL } from 'services/variables';

const MovieDetails = () => {
  const { query } = useParams();
  const [movie, setMovie] = useState({});

  const location = useLocation();
  const backLinkLocationRef = useRef(location.state?.from ?? '/movies');
  // console.log('backLinkLocationRef:', backLinkLocationRef);
  // console.log('location:', location);
  // console.log('movie:', movie);

  useEffect(() => {
    getMovieDetails(query).then(res => {
      setMovie(res);
    });
  }, [query]);

  return (
    <div>
      <h1>MovieDetails ðŸŽ¬ {movie.title}</h1>
      <Back to={backLinkLocationRef.current}>
        <svg
          height="16"
          width="16"
          xmlns="http://www.w3.org/2000/svg"
          version="1.1"
          viewBox="0 0 1024 1024"
        >
          <path d="M874.690416 495.52477c0 11.2973-9.168824 20.466124-20.466124 20.466124l-604.773963 0 188.083679 188.083679c7.992021 7.992021 7.992021 20.947078 0 28.939099-4.001127 3.990894-9.240455 5.996574-14.46955 5.996574-5.239328 0-10.478655-1.995447-14.479783-5.996574l-223.00912-223.00912c-3.837398-3.837398-5.996574-9.046027-5.996574-14.46955 0-5.433756 2.159176-10.632151 5.996574-14.46955l223.019353-223.029586c7.992021-7.992021 20.957311-7.992021 28.949332 0 7.992021 8.002254 7.992021 20.957311 0 28.949332l-188.073446 188.073446 604.753497 0C865.521592 475.058646 874.690416 484.217237 874.690416 495.52477z"></path>
        </svg>
        <span>Back</span>
      </Back>
      <div>
        <Section>
          <Box>
            <img src={`${IMG_URL}${movie.poster_path}`} alt={movie.title} />
          </Box>
          <div>
            <section>
              <div>
                <h2>
                  {movie.title} <span>({movie.release_date})</span>
                </h2>
              </div>
              <div>
                <h3>Overview</h3>
                <p>{movie.overview}</p>

                <h3>Ganres</h3>
                <p>{movie.genres?.map(({ name }) => name).join(', ')}</p>
                <p>
                  <span>
                    <b>Runtime:</b>
                  </span>
                  {movie.runtime}
                </p>
              </div>
              <ul>
                <li>
                  <p>
                    <span>
                      <b>Riting: </b>
                    </span>
                    {movie.vote_average}
                  </p>
                </li>
              </ul>
            </section>
          </div>
        </Section>
      </div>
      <h2>Aditional information</h2>
      <LinkList>
        <li>
          <StyledLink to={'cast'}>Cast</StyledLink>
        </li>
        <li>
          <StyledLink to={'reviews'}>Reviews</StyledLink>
        </li>
      </LinkList>
      <Suspense fallback={<div>Loading...</div>}>
        <Outlet />
      </Suspense>
    </div>
  );
};

export default MovieDetails;
